import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { NavigationStart, Router, RouterEvent } from '@angular/router';
import * as dayjs from 'dayjs';
import * as utc from 'dayjs/plugin/utc';
import { filter, firstValueFrom, take, tap } from 'rxjs';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { XADropdownConfig, XADropdownOption } from 'src/app/model/xa-dropdown-model';
import { BookingService } from 'src/app/services/booking/booking.service';
import { CommonService } from 'src/app/services/common/common.service';

dayjs.extend(utc)

@Component({
  selector: 'app-no-show',
  templateUrl: './no-show.component.html',
  styleUrls: [ './no-show.component.scss' ]
})
export class NoShowComponent implements OnInit{
  public sendButton: ChqWidgetsButtonModel = {
    label: 'send',
    type: 'primary',
    icon: 'send',
    onclick: this.closeDialog.bind(this)
  };

  public reasonConfig: XADropdownConfig = {
    name: 'singleSelect',
    label: 'reason',
    showRequiredLabel: true,
    placeHolder: 'reason',
    valueKey: 'id',
    labelKey: 'description',
    dropdownPosition: 'relative'
  };

  public reasonOptions: XADropdownOption[];

  public noShowForm = new FormGroup({
    Reason: new FormControl(null, [ Validators.required ])
  });

  /**
   * 
   * @param dialog 
   * @param dialogRef 
   * @param data 
   * @param router 
   */
  constructor(
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<NoShowComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private router: Router,
    private bookingService: BookingService,
    private commonService: CommonService
  ) {
    // Close dialog ref on route changes
    router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationStart),
      tap(() => this.dialogRef.close()),
      take(1),
    ).subscribe();
  }

  /**
   * detects changes in input attributes
   * @param changes 
   */
  ngOnInit(): void {
    this.populateReasons();
  }

  /**
   * populate reasons
   */
  public async populateReasons(): Promise<void> {
    this.commonService.showLoading();
    this.reasonOptions = (await firstValueFrom(this.bookingService.getNoShowReason()))?.data;

    this.commonService.hideLoading();
  }

  /**
   * 
   */
  saveChanges(): void {
    this.commonService.showLoading();

    const patchData = [
      {
        'op': 'replace',
        'path': 'RepairNoShowReasonId',
        'value': this.noShowForm.value.Reason
      } 
    ];
      
    this.bookingService.patchRepair(patchData, this.data).subscribe({
      next: async (result: any) => {
        if (result) {
          this.dialogRef.close({ 'success': true });
        }

        this.commonService.hideLoading();
      },
      error: (err) => {
        this.commonService.hideLoading();
      }
    });
  }
  
  /**
   * 
   */
  closeDialog(): void {
    this.dialogRef.close({ 'success': false });
  }

}