import { DragDropModule } from '@angular/cdk/drag-drop';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { Route, RouterModule } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { CdTimerModule } from 'angular-cd-timer';
import { DxDateBoxModule } from 'devextreme-angular';
import { NgxPrintModule } from 'ngx-print';
import { SchedulePickupDatePopupComponent } from 'src/app/dialogs/schedule-pickup-date-popup/schedule-pickup-date-popup.component';
import { SendInvoiceComponent } from 'src/app/dialogs/send-invoice/send-invoice.component';
import { VersionControlResolver } from 'src/app/resolvers/versionControl.resolver';
import { ErrorModule } from 'src/app/shared/error-page/error.module';
import { HistoryComponent } from 'src/app/shared/history/history.component';
import { SharedModule } from 'src/app/shared/shared.module';
import { WidgetsModule } from 'src/app/widgets/widgets.module';
import { InvoiceSummaryComponent } from '../../../dialogs/invoice-summary/invoice-summary.component';
import { EstimateComponent } from './booking/estimate/estimate/estimate.component';
import { PersonalInfoComponent } from './booking/personal-info/personal-info.component';
import { AddPhotosComponent } from './booking/repair-add-photos/add-photos.component';
import { RepairNewDocumentsComponent } from './booking/repair-new-documents/repair-new-documents/repair-new-documents.component';
import { CustomerLayoutComponent } from './customer-layout/customer-layout.component';
import { GenerateInvoicesComponent } from './repair/generate-invoices/generate-invoices.component';
import { RepairInvoicesComponent } from './repair/invoices/invoices.component';
import { RepairPartsComponent } from './repair/parts/repair-parts.component';
import { RepairPhotosComponent } from './repair/repair-stages/repair-photos/repair-photos.component';
import { RepairStagesTableComponent } from './repair/repair-stages/repair-stages-table/repair-stages-table.component';
import { RepairStagesComponent } from './repair/repair-stages/repair-stages.component';
import { DirectiveModule } from 'src/app/helper/directive/directive.module';
import { NgScrollbarModule } from 'ngx-scrollbar';
import { MatTableModule } from '@angular/material/table';
import { AddBookingComponent } from './booking/personal-info/add-booking/add-booking.component';
import { MatDialogModule } from '@angular/material/dialog';
import { MaterialModule } from 'src/app/material.module';
import { PipeModule } from 'src/app/helper/pipe.module';
import { DeliveryServiceComponent } from '../delivery-service/delivery-service.component';
import { NoShowComponent } from './booking/personal-info/no-show/no-show.component';


const customerRoutes: Route[] = [
  {
    path: '',
    component: PersonalInfoComponent,
    //canDeactivate: [ CanDeactivateGuard ],
    //resolve: { version: VersionControlResolver }
  },
  {
    path: 'refresh',
    component: PersonalInfoComponent
  },
  {
    path: 'document',
    component: RepairNewDocumentsComponent,
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'photos',
    component: AddPhotosComponent,
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'estimate',
    component: EstimateComponent,
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'parts',
    component: RepairPartsComponent,
    resolve: { version: VersionControlResolver }
  },
  {
    path: 'repair-stages',
    component: RepairStagesComponent,
    resolve: { version: VersionControlResolver }
  },
  {
    path: 'invoices',
    component: RepairInvoicesComponent,
    resolve: { version: VersionControlResolver },
  },
  {
    path: 'invoicesEdit',
    component: GenerateInvoicesComponent,
    resolve: { version: VersionControlResolver },
  },
];

@NgModule({
  declarations: [
    PersonalInfoComponent,
    CustomerLayoutComponent,
    AddPhotosComponent,
    RepairNewDocumentsComponent,
    EstimateComponent,
    RepairPartsComponent,
    RepairStagesComponent,
    RepairStagesTableComponent,
    RepairPhotosComponent,
    RepairInvoicesComponent,
    InvoiceSummaryComponent,
    GenerateInvoicesComponent,
    SchedulePickupDatePopupComponent,
    SendInvoiceComponent,
    AddBookingComponent,
    DeliveryServiceComponent,
    NoShowComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(customerRoutes),
    WidgetsModule,
    SharedModule,
    DragDropModule,
    ErrorModule,
    CdTimerModule,
    TranslateModule,
    HistoryComponent,
    NgxPrintModule,
    DxDateBoxModule,
    DirectiveModule,
    NgScrollbarModule,
    MatTableModule,
    MatDialogModule,
    MaterialModule,
    PipeModule
  ],
  exports: [
    RouterModule,
    CustomerLayoutComponent,
    AddPhotosComponent,
    NgScrollbarModule 
  ]
})
export class AddRepairCustomersModule { }
