/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable require-jsdoc */
import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { NavigationStart, Router, RouterEvent } from '@angular/router';
import * as dayjs from 'dayjs';
import * as utc from 'dayjs/plugin/utc';
import { filter, firstValueFrom, take, tap } from 'rxjs';
import { endDateValidators } from 'src/app/helper/form/customer-form.helper';
import { ChqWidgetsButtonModel } from 'src/app/model/chq-widgets-button-model';
import { XADropdownConfig, XADropdownOption } from 'src/app/model/xa-dropdown-model';
import { BookingService } from 'src/app/services/booking/booking.service';
import { CalendarService } from 'src/app/services/calendar/calendar.service';
import { CommonService } from 'src/app/services/common/common.service';
import { SettingsService } from 'src/app/services/settings/settings.service';

dayjs.extend(utc)

@Component({
  selector: 'app-add-booking',
  templateUrl: './add-booking.component.html',
  styleUrls: [ './add-booking.component.scss' ],
  providers: [ CalendarService ]
})
export class AddBookingComponent implements OnInit {
  public PageReady: boolean = false;
  public sendButton: ChqWidgetsButtonModel = {
    label: 'send',
    type: 'primary',
    icon: 'send',
    onclick: this.closeDialog.bind(this)
  };

  public bookingTypeConfig: XADropdownConfig = {
    name: 'singleSelect',
    label: 'booking_type',
    showRequiredLabel: true,
    placeHolder: 'booking_type',
    labelKey: 'name',
    valueKey: 'id'
  };

  public bookingTypeOptions: XADropdownOption[];

  public minDate = new Date(new Date().setHours(0, 0, 0, 0));

  public bookingForm = new FormGroup({
    BookingType: new FormControl({ value: null }, [ Validators.required ]),
    StartDate: new FormControl({ value: null }, [ Validators.required ]),
    EndDate: new FormControl(null, [ Validators.required, endDateValidators('StartDate') ])
  });

  constructor(
    public dialog: MatDialog,
    public dialogRef: MatDialogRef<AddBookingComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private router: Router,
    private bookingService: BookingService,
    private commonService: CommonService,
    private settingService: SettingsService
  ) {
    // Close dialog ref on route changes
    router.events.pipe(
      filter((event: RouterEvent) => event instanceof NavigationStart),
      tap(() => this.dialogRef.close()),
      take(1),
    ).subscribe();
  }


  /**
   * detects changes in input attributes
   * @param changes 
   */
  ngOnInit(): void {
    this.populateDropdowns();
    this.setEndDate();
    this.setRepairDate();

    this.bookingForm.get('BookingType').valueChanges.subscribe((value) => {
      const selectedType = this.bookingTypeOptions.find(x=>x['id'] == value);

      this.bookingForm.controls.StartDate.setValue(this.data.startDate || new Date());
      this.bookingForm.controls.StartDate.enable();

      if(selectedType['duration'] == 0) {
        this.bookingForm.controls.EndDate.enable();
      } else {
        this.bookingForm.controls.EndDate.disable();        
      }

      this.setEndDate();
      this.setRepairDate();
    });

    this.bookingForm.get('StartDate').valueChanges.subscribe((value) => {
      this.setEndDate();
    });
  }

  /**
   * 
   */
  private setEndDate():void {
    if(this.bookingForm.controls.BookingType.value){
      const selectedType = this.bookingTypeOptions.find(x=>x['id'] == this.bookingForm.controls.BookingType.value);

      if(selectedType['duration'] as number > 0) {
        const endDate = dayjs(this.bookingForm.controls.StartDate.value).
          add(selectedType['duration'] as number, 'minutes').toDate();
        this.bookingForm.controls.EndDate.setValue(endDate);
        this.bookingForm.controls.EndDate.disable();
      }
    } else if(!this.data.EndDate) {

      const endDate = dayjs(this.bookingForm.controls.StartDate.value).
        add(30, 'minutes').toDate()
      this.bookingForm.controls.EndDate.setValue(endDate);
    }
  }

  private setRepairDate(): void {
    if(this.bookingForm.controls.BookingType.value){
      const selectedType = this.bookingTypeOptions.find(x=>x['id'] == this.bookingForm.controls.BookingType.value);
      
      if(selectedType['name'] == 'Repair' && this.data.repairStartDate && this.data.repairEndDate) {
        this.bookingForm.controls.StartDate.setValue(this.data.repairStartDate);
        this.bookingForm.controls.EndDate.setValue(this.data.repairEndDate);

        this.bookingForm.controls.StartDate.disable();
        this.bookingForm.controls.EndDate.disable();
      }

      if(selectedType['name'] == 'Booking In' && this.data.repairStartDate) {
        this.bookingForm.controls.StartDate.setValue(this.data.repairStartDate);
        this.bookingForm.controls.EndDate.setValue(dayjs.utc(this.data.repairStartDate).add(selectedType['duration'] || 30, 'minutes').toDate());

        this.bookingForm.controls.StartDate.disable();
        this.bookingForm.controls.EndDate.disable();
      }

      if(selectedType['name'] == 'Booking Out' && this.data.repairEndDate) {
        this.bookingForm.controls.StartDate.setValue(dayjs.utc(this.data.repairEndDate).subtract(selectedType['duration'] || 30, 'minutes').toDate());
        this.bookingForm.controls.EndDate.setValue(this.data.repairEndDate);

        this.bookingForm.controls.StartDate.disable();
        this.bookingForm.controls.EndDate.disable();
      }
    }
  }

  /**
   * populate dropdowns
   */
  public async populateDropdowns(): Promise<void> {
    this.commonService.showLoading();

    if(this.data.bookingTypes) {
      this.bookingTypeOptions = this.data.bookingTypes;
    } else {
      const serviceTypes = await firstValueFrom(this.settingService.getBookingRepairType());
      this.bookingTypeOptions = serviceTypes?.data?.BookingTypes;
    }

    this.bookingTypeOptions.splice(0, 0, {
      id: null,
      name: 'Select'
    });

    if(this.data) {
      this.bookingForm.controls.BookingType.setValue(this.data.bookingTypeId || null);
      if(this.data.editMode) {
        this.bookingTypeConfig.disabled = true;
      }
      this.bookingForm.controls.StartDate.setValue(this.data.startDate || new Date());
      this.bookingForm.controls.EndDate.setValue(this.data.endDate || null);
    } 
    
    this.PageReady = true;
    this.commonService.hideLoading();
  }

  saveChanges(): void {
    const formValue = this.bookingForm.getRawValue();

    const startDateString = dayjs(formValue.StartDate.toString()).utc().format('YYYY-MM-DDTHH:mm:ss.000') + 'Z';
    const endDateString = dayjs(formValue.EndDate.toString()).utc().format('YYYY-MM-DDTHH:mm:ss.000') + 'Z';

    const newData: any = {
      domainID: this.data.domainID,
      objectID: this.data.objectID,
      objectGuid: this.data.objectGuid,
      objectData: this.data.objectData,
      bookingTypeId: formValue.BookingType,
      bookingTypeName: this.bookingTypeOptions.find(x=>x['id'] == formValue.BookingType)['name'],
      startDate: startDateString,
      endDate: endDateString
    };
   
    if(this.data?.editMode) {
      newData.bookingGuid = this.data.bookingGuid;
      newData.bookingId = this.data.bookingId;
      newData.Action = 'update';

      this.dialogRef.close({ 'success': true, 'data': newData });
    } else {
      newData.customer = this.data.customerData;
      newData.workProvider = this.data.workProviderData;
      newData.Action = 'create';

      this.dialogRef.close({ 'success': true, 'data': newData });
    }
  }

  closeDialog(): void {
    this.dialogRef.close({ 'success': false, 'data': null });
  }

}